<template>
  <v-app-bar
    :color="landingPageBackgroundColour"
    elevation="0"
    extended
    extension-height="70"
    :height="isMobile"
  >
    <v-container class="px-12">
      <v-row align="center" justify="center">
          <v-img
            :src="zoombaLogoUrl"
            alt="Zoomba Logo"
            contain
            max-height="57"
            max-width="205"
          />

        <v-spacer/>

        <v-row justify="center">
          <v-btn
            :color="landingPageTextColour"
            class="text-capitalize mr-5 py-5 nav-bar-font"
            text
            @click="scrollToContent"
          >
            About
          </v-btn>
          <v-btn
            :color="landingPageTextColour"
            class="text-capitalize py-5 nav-bar-font"
            text
            @click="scrollToContact"
          >
            Contact
          </v-btn>
        </v-row>

        <v-btn
          :color="landingPageTextColour"
          class="text-capitalize py-5 px-7 white-border-text-btn"
          text
          @click="redirectToLoginPage"
        >
          Login
        </v-btn>
      </v-row>
    </v-container>
  </v-app-bar>
</template>

<script>
import { logoAssetLocation, palette } from '@/mixins/interface'
import { loginRoute } from '@/utils/urls'

export default {
  name: 'NavBar',
  mixins: [logoAssetLocation, palette],

  computed: {
    isMobile () {
      return this.$vuetify.breakpoint.smAndDown ? '200' : '100'
    }
  },

  methods: {
    scrollToContent () {
      this.$vuetify.goTo('#content')
    },

    scrollToContact () {
      this.$vuetify.goTo('#contact')
    },

    redirectToLoginPage () {
      this.$router.push(loginRoute)
    }
  }
}
</script>

